import { useState } from "react";
import {
  Section,
  Select,
  InputSkillSet,
  muiSearchIcon,
} from "./ParamInput.style";
import SearchIcon from "@mui/icons-material/Search";
import { useStateValue } from "store/StateProvider";

function ParamInput() {
  const [initialState, dispatch] = useStateValue(); // eslint-disable-line no-unused-vars
  const [nation, setNation] = useState(null);
  const [employeeType, setEmployeeType] = useState(null);
  const [searchParams, setSearchParams] = useState(null);

  const handleNation = (e) => {
    if (e.target.value === "null") {
      setNation(null);
    } else {
      setNation(e.target.value);
    }
  };
  const handleType = (e) => {
    if (e.target.value === "null") {
      setEmployeeType(null);
    } else {
      setEmployeeType(e.target.value);
    }
  };
  const handleSearch = (e) => {
    if (e.target.value === "") {
      setSearchParams(null);
    } else {
      setSearchParams(e.target.value);
    }
  };

  // const SetInput = () => {
  //   dispatch({
  //     type: `SetInput`,
  //     nation: nation,
  //     employeeType: employeeType,
  //   });
  // };
  return (
    <Section>
      <Select onChange={handleNation}>
        <option value="null">--   --</option>
        <option value="INDIA"></option>
        <option value="CHINA"></option>
        <option value="VIETNAM"></option>
        <option value="PHILIPPINE"></option>
      </Select>
      <Select onChange={handleType}>
        <option value="null">--   --</option>
        <option value="FULLTIME"></option>
        <option value="PARTTIME"></option>
        <option value="FREELANCER"></option>
      </Select>
      <InputSkillSet onChange={handleSearch} placeholder=" ." />
      <SearchIcon
        sx={muiSearchIcon}
        onClick={() =>
          dispatch({
            type: `SetInput`,
            nation: nation,
            employeeType: employeeType,
            searchParams: searchParams,
          })
        }
      />
    </Section>
  );
}

export default ParamInput;
